    </template>
    <template #description>
      <p>{{ $t('donate.support.text-1') }}</p>
      <p>{{ $t('donate.support.text-2') }}</p>
      <p>{{ $t('donate.support.text-3') }}</p>
    </template>
  </Support>
  <Faq :list="formattedFaqList" />
</template>

